.btn {
  display: inline-flex;
  padding: 10px 20px;

  color: $color-default-white;

  background-color: $color-transparent;
  border: none;
  outline: 4px solid $color-default-white;
  cursor: pointer;
  user-select: none;
  transition: opacity $trans-default;

  font-family: $font-roboto-sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  display: inline-flex;
  vertical-align: baseline;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &:hover {
    background: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
    outline: none;
  }

  &--dark {
    color: #102654;
    outline: 2px solid #102654;

    &:hover {
      color: $color-default-white;
    }
  }
}
