[data-swiper-2-prev].disabled:hover::before, [data-swiper-2-prev].disabled:hover::after,
[data-swiper-2-next].disabled:hover::before,
[data-swiper-2-next].disabled:hover::after {
  background-color: #afafaf;
}

.feedback__wrapper {
  padding: 0;
  margin: 0 auto;
  max-width: 1366px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.form {
  color: #ffffff;
  background-color: #102654;
}

.form__wrapper {
  padding: 56px clamp(30px, 24.8901903367%, 170px) 70px clamp(30px, 15.0805270864%, 103px);
}
.form__wrapper h2 {
  margin: 0;
  padding: 0;
  font-family: "Alumni Sans", "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-weight: 800;
  font-size: 54px;
  line-height: 156%;
  letter-spacing: 0.03em;
}
.form__wrapper button {
  width: 100%;
  justify-content: center;
  margin-top: 30px;
  grid-column: 1/-1;
}
.form__wrapper form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
}
@keyframes autofill {
  0%, 100% {
    background: transparent;
  }
}
.form__wrapper input {
  padding: 12px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #74819c;
  background: none;
  position: relative;
  color: #ffffff;
  width: 100%;
  box-sizing: border-box;
}
.form__wrapper input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  background: none;
}
.form__wrapper input:-webkit-autofill {
  background-clip: none;
}
.form__wrapper input:invalid {
  outline-color: #ed0233;
}

.is-invalid:focus-visible + span {
  display: block;
}

.feedback__input-name,
.feedback__input-phone {
  position: relative;
}
.feedback__input-name span,
.feedback__input-phone span {
  display: none;
  position: absolute;
  top: 55px;
  left: -14px;
  padding: 5px;
  color: #ed0233;
  z-index: 999;
  font-size: 18px;
  line-height: 25px;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 5px 5px 5px #000000;
}
.feedback__input-name span::before,
.feedback__input-phone span::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 10px;
  background-color: #ffffff;
  top: -10px;
  left: 11px;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

